:root {
  --bodyMaxWidth: 1240px;
}
.flat-row {
  display: -ms-flexbox;
  display: flex;
  max-width: var(--bodyMaxWidth);
  margin: auto;
}
.flat-col {
  -ms-flex-preferred-size: 55%;
      flex-basis: 55%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.flat-map {
  -ms-flex-preferred-size: 45%;
      flex-basis: 45%;
  height: 100vh;
  width: 100%;
}
.maps-google {
  height: 100vh;
  width: 100%;
}


@media (max-width: 600px) {
  .flat-row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .maps-google {
    height: 70vh;
  }
}


