.marker-map {
    width: 60px;
    border-radius: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    height: 30px;
    border: 2px solid seashell;
    background-color: seashell;
    color: #000;
    font-size: 14px;
}
.marker-map.marker-place {
    background-color: rgb(40, 36, 117);
    color: #fff;
    font-weight: bold;
}


